$primaryColor: rgb(42, 171, 226);
$primaryFont: 'Sacramento', cursive;
$secondaryFont: 'Fredericka the Great', cursive;

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

.loadingContainer{
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.loadingAnimationContainer{
  opacity: 0;
  height: 150px;
  width: 150px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;

  .loadingAnimationContainer2{
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -25;
  }

  .circle{
    border-radius: 50%;
  }

  .one{
    height: 70px;
    width: 70px;
    position: absolute;
    left: -15px;
    bottom: 10px;
    background: $primaryColor;
  }

  .two{
    height: 20px;
    width: 20px;
    background: $primaryColor;
  }

  .three{
    height: 70px;
    width: 70px;
    position: absolute;
    background: $primaryColor;
    bottom: 10px;
    right: -15px;
  }

  .four{
    height: 70px;
    width: 70px;
    position: absolute;
    background: $primaryColor;
    top: -20px;
    right: 40px;
  }

  .five{
    height: 20px;
    width: 20px;
    bottom: 10px;
    left: -45px;
    position: absolute;
    background: $primaryColor;
  }

  .six{
    height: 20px;
    width: 20px;
    bottom: 10px;
    right: -45px;
    position: absolute;
    background: $primaryColor;
  }

  .seven{
    height: 20px;
    width: 20px;
    position: absolute;
    right: 65px;
    top: -50px;
    background: $primaryColor;
  }
}


.bgVid{
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -50;
    transform: translateX(-50%) translateY(-50%);
    transition: 2s opacity;
}

.transitionWhite{
  background: white;
  height: 100vh;
  width: 150%;
  position: fixed;
  left: -25%;
  top: 0;
  z-index: 50;
}

.transitionBlue{
  background: rgb(42, 171, 226);
  height: 100vh;
  width: 150%;
  position: fixed;
  left: -25%;
  top: 0;
  z-index: 25;
}

.homeContainer{
  z-index: 0;
  font-family: 'Muli', sans-serif;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: transparent;
  color: white;

  .homeTitle{
    text-align: center;
    width: 500px;
    height: 200px;
    letter-spacing: 1px;
    h1{
      font-family: $primaryFont;
      font-size: 100px;
      margin-bottom: 20px;
    }
    h6{
      font-family: $secondaryFont;
      font-size: 20px;
      color: white;
    }
  }

  .homeCircleContainer{
    width: 440px;
    margin-right: 100px;
    letter-spacing: 1px;
    .homeBigCircle{
      display: flex;
      flex-direction: column;
      position: relative;
      align-items: center;
      color: white;
      justify-content: center;
      text-align: center;
      background: transparent;
      width: 300px;
      height: 300px;
      border-radius: 50%;
      border: 3px solid $primaryColor;
      h3{
        font-family: $secondaryFont;
        font-size: 18px;
      }
      h2{
        margin: 10px;
        font-family: $primaryFont;
        font-size: 40px;
        color: $primaryColor;
      }
    }
    .homeBigCircle:before {
      content: " ";
      position: absolute;
      border-radius: 50%;
      z-index: -1;
      top: -8px;
      left: -8px;
      right: 2px;
      bottom: 2px;
      border: 3px solid $primaryColor;
    }
    .homeSmallCircle{
      color: white;
      font-family: $secondaryFont;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      background: transparent;
      margin-left: 275px;
      margin-bottom: -50px;
      width: 150px;
      height: 150px;
      border-radius: 50%;
      border: 3px solid $primaryColor;
    }
    .homeSmallCircle:before {
      content: " ";
      position: absolute;
      border-radius: 50%;
      z-index: -1;
      top: -8px;
      left: -8px;
      right: 2px;
      bottom: 2px;
      border: 3px solid $primaryColor;
    }
  }
}

.applicationContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  .devMountainTimelineContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgb(42, 171, 226);
    padding: 20px;
    h3{
      color: white;
      font-size: 36px;
      margin: 20px;
    }
    .devMountainTimeline{
      width: 60%;
      background: rgb(42, 171, 226);
    }
  }
  .applicationTitle{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 30px;
    font-size: 34px;
    font-weight: bold;
    color: $primaryColor;
    background-color: rgba(0,0,0,0.05);
    font-family: Lucida Grande;
  }
  .courseContainer{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    .course{
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 30%;
      border-radius: 1em;
      background-color: rgba(0,0,0,0.05);
      margin: 10px 0px 20px 0px;
      h1{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 90%;
        height: 30px;
        margin: 10px;
        background: $primaryColor;
        color: white;
        border-radius: 2em;
        font-size: 16px;
        font-weight: bold;
        font-family: Lucida Grande;
      }
      img{
        width: 250px;
        height: 250px;
        border-radius: 50%;
      }
      .courseInfo{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 90%;
        margin: 10px;
        p{
          display: flex;
          align-items: center;
          justify-content: center;
          height: 30px;
          width: 45%;
          background: $primaryColor;
          color: white;
          border-radius: 2em;
          padding: 5px 10px;
          margin: 3px;
        }
      }
      button{
        border: none;
        outline: none;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 30px;
        width: 45%;
        background: #88CE02;
        color: white;
        border-radius: 2em;
        padding: 5px 10px;
        margin: 3px 0px 10px 0px;
        font-size: 16px;
        font-weight: bold;
        letter-spacing: 1px;
        font-family: Lucida Grande;
      }
    }
  }
}

