  .provoChoice{
    position: relative;
    height: 100%;
    width: 33%;
    overflow: hidden;
    .provoChoiceContent{
      font-family: $primaryFont;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      background-image: url("https://s-media-cache-ak0.pinimg.com/736x/98/b3/31/98b331a6197902e3f8c3058433f6f3a2.jpg");
      background-size: cover;
      background-position: center;
    }
  }
  .slcChoice{
    position: relative;
    height: 100%;
    width: 33%;
    overflow: hidden;
    .slcChoiceContent{
      font-family: $primaryFont;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      background-image: url("https://s-media-cache-ak0.pinimg.com/736x/fc/fa/5a/fcfa5ac1267d8ef0302b776e28683362.jpg");
      background-size: cover;
      background-position: center;
    }
  }
}
