.provoHousingContainer{
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  overflow: hidden;
  .housingTopHalf{
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 60%;
    .housingVideo{
      height: 100%;
      width: 50%;
      overflow: hidden;
      position: relative;
      video{
        position: absolute;
        top: 50%;
        left: 50%;
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
        z-index: -50;
        transform: translateX(-50%) translateY(-50%);
        transition: 2s opacity;
      }
    }
    .housingTitle{
      width: 50%;
      height: 100%;
      position: relative;
      overflow: hidden;
      background: rgba(0,0,0,0.50);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      h1{
        font-size: 80px;
        font-family: $primaryFont;
        color: $primaryColor;
        margin-top: 10px;
      }
      h2{
        font-size: 32px;
        font-family: $secondaryFont;
        color: white;
      }
    }
  }
  .housingBottomHalf{
    width: 100%;
    height: 40%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    .housingImage{
      overflow: hidden;
      position: relative;
      height: 200px;
      width: 200px;
      border-radius: 50%;
      img{
        border-radius: 50%;
        border: 1px solid white;
        width: 100%;
        height: 100%;
      }
    }
  }
}

