    position: absolute;
    border-radius: 50%;
    background: rgba(0,0,0,0.25);
    z-index: -1;
    top: -8px;
    left: -8px;
    right: 2px;
    bottom: 2px;
    border: 3px solid $primaryColor;
  }
}

.upDown{
  display: flex;
  position: fixed;
  background: rgba(0,0,0,0.50);
  right: 20px;
  bottom: 0;
  width: 50px;
  height: 30px;
  z-index: 500;
}
.arrowContainer{
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.arrow{
  display: flex;
  align-items: center;
  justify-content: center;
  border: solid white;
  border-width: 0 1px 1px 0;
  display: inline-block;
  padding: 5px;
}

.upIcon{
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.downIcon{
  margin-bottom: 12px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
